var codeStyle = function () {
    var items = document.querySelectorAll(".codeStyle");
    if (!items.length) return;
    var keyWord1 = /\s(true|function|import|from|for|false)/g;
    var keyWord2 = /\s(class|id|type)=</g;
    items.forEach(function (item) {
        var html = item.innerHTML;
        html = html.replace(/(\"[^""]+\")/g,'<span class="atv">$1</span>');
        html = html.replace(keyWord1,'<span class="atn"> $1</span>');
        html = html.replace(keyWord2,' <span class="pun">$1</span>=<');
        html = html.replace(/^\s{5,12}/mg,'');
        html = html.replace(/(\/\/.*)/g,'<span class="gray">$1</span>');
        
        html = html.replace(/&gt;(.+)&lt;\//g,'&gt;<span class="cfff">$1</span>&lt;\/');
        

        item.innerHTML = html;
    }); 
}
var showDetail = function() {
    var isHideTableCss = "isHideTable";
    var items = document.querySelectorAll(".showDetail");

    items.forEach(function(item) {
        var parent = item.parentNode;
        item.onclick = function() {
            parent.classList[parent.classList.contains(isHideTableCss) ? "remove" : "add"](isHideTableCss)
        }
    });
    items.forEach(function(item) {
        item.parentNode.classList.add(isHideTableCss);
    });
}

var trim = function( text ) {
    return (text || "").replace( /^\s+|\s+$/g, "" );
}
export {codeStyle, showDetail,trim}


